import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getCards, getAllTypes } from '../../store/actions/card';
import { URL, ALL_TYPES_URL } from '../../utils/index';
import Card from '../card/Card';
import CardMin from '../card/CardMin';
import CardView from '../card-view/CardView';
import Spinner from '../layout/Spinner';

const Cards = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getCards(URL));
    dispatch(getAllTypes(ALL_TYPES_URL));
  }, [dispatch]);

  const allCards = useSelector(state => state.card.allCards);
  const currentCardId = useSelector(state => state.card.cardViewId);
  const nextUrl = useSelector(state => state.card.nextUrl);

  const loading = useSelector(state => state.card.loading);
  if (loading) {
    return <Spinner />;
  }

  const loadMore = () => {
    dispatch(getCards(nextUrl));
  };

  const DefaultLayout = () => {
    return (
      <div className='container'>
        <div className='cards'>
          {allCards.map((card, idx) => {
            return <Card key={idx} card={card} id={idx} />;
          })}
          <button className='btn-more' onClick={() => loadMore()}>
            Load more
          </button>
        </div>
        {currentCardId !== null ? (
          <CardView info={allCards[currentCardId]} />
        ) : null}
      </div>
    );
  };

  const MinLayout = () => {
    return (
      <div className='container'>
        <div className='cards'>
          {allCards.map((card, idx) => {
            return <CardMin key={idx} card={card} id={idx} />;
          })}
          <button className='btn-more' onClick={() => loadMore()}>
            Load more
          </button>
        </div>
      </div>
    );
  };

  return document.body.clientWidth > 800 ? <DefaultLayout /> : <MinLayout />;
};

export default Cards;
