import { GET_CARDS, GET_CARD_ID, GET_ALL_TYPES, FILTER } from '../types';
import axios from 'axios';

// Get cards
export const getCards = url => async dispatch => {
  try {
    axios.get(url).then(res => {
      const allCards = [];
      const nextUrl = res.data.next;

      res.data.results.forEach(item => {
        allCards.push(axios.get(item.url));
      });

      Promise.all(allCards).then(res => {
        return dispatch({
          type: GET_CARDS,
          payload: { res, nextUrl }
        });
      });
    });
  } catch (err) {
    console.log(err);
  }
};

// Get current card's id
export const getCardId = id => async dispatch => {
  return dispatch({
    type: GET_CARD_ID,
    payload: id
  });
};

// Get all types
export const getAllTypes = url => async dispatch => {
  try {
    axios.get(url).then(res => {
      const allTypes = [];

      res.data.results.forEach((i, idx) => {
        allTypes[idx] = i.name;
      });

      return dispatch({
        type: GET_ALL_TYPES,
        payload: allTypes
      });
    });
  } catch (err) {
    console.log(err);
  }
};

// Filter
export const filter = (type = 'all', allCards) => async dispatch => {
  const filtered = allCards.filter(i => {
    if (type === 'all') {
      return true;
    }

    let includes = false;
    i.types.forEach(t => {
      if (t.type.name === type) {
        includes = true;
        return true;
      }
      return false;
    });
    return includes;
  });

  return dispatch({
    type: FILTER,
    payload: filtered
  });
};
