import React, { useState } from 'react';
import Features from './Features/Features';
import { useSelector } from 'react-redux';

const CardMin = props => {
  const [isClicked, setIsClicked] = useState(false);
  const allCards = useSelector(state => state.card.allCards);
  const info = allCards[props.id];

  const toUpperCase = str => {
    return str.charAt(0).toUpperCase() + str.slice(1);
  };

  let name = '';
  if (info.id < 10) {
    name = toUpperCase(info.forms[0].name) + ' #00' + info.id;
  } else {
    name = toUpperCase(info.forms[0].name) + ' #0' + info.id;
  }

  const type = info.types.map(i => toUpperCase(i.type.name)).join(', ');

  const PoorCard = () => {
    return (
      <>
        <p className='title'>
          <b>{props.card.forms[0].name}</b>
        </p>
        <Features types={props.card.types} />
      </>
    );
  };

  const FullCard = () => {
    return (
      <>
        <p className='title'>
          <b>{name}</b>
        </p>
        <table className='cardmin-table'>
          <tbody>
            <tr>
              <td>Type</td>
              <td>{type}</td>
            </tr>
            <tr>
              <td>Attack</td>
              <td>{info.stats[4].base_stat}</td>
            </tr>
            <tr>
              <td>Defense</td>
              <td>{info.stats[3].base_stat}</td>
            </tr>
            <tr>
              <td>HP</td>
              <td>{info.stats[5].base_stat}</td>
            </tr>
            <tr>
              <td>SP Attack</td>
              <td>{info.stats[2].base_stat}</td>
            </tr>
            <tr>
              <td>SP Defense</td>
              <td>{info.stats[1].base_stat}</td>
            </tr>
            <tr>
              <td>Speed</td>
              <td>{info.stats[0].base_stat}</td>
            </tr>
            <tr>
              <td>Weigth</td>
              <td>{info.weight}</td>
            </tr>
            <tr>
              <td>Total moves</td>
              <td>{info.moves.length}</td>
            </tr>
          </tbody>
        </table>
      </>
    );
  };

  return (
    <div
      className='card'
      onClick={() => {
        setIsClicked(!isClicked);
      }}
    >
      <img
        className='image'
        src={props.card.sprites.front_default}
        alt={name}
      />

      {!isClicked ? <PoorCard /> : <FullCard />}
    </div>
  );
};

export default CardMin;
